<template>
  <div class='login_container'>
    <div class='login_box'>
      <!-- 头像区域-->
      <div class='avatar_box '>
        <img src='../assets/logo.png' alt=''>
      </div>
      <!--  登录表单区域-->
      <el-form ref='loginFormRef' :model='loginForm' :rules='loginFormRules' label-width='0px' class='login_form'>
        <!-- 用户名-->
        <el-form-item prop='username'>
          <el-input v-model='loginForm.username' prefix-icon='iconfont icon-user'></el-input>
        </el-form-item>
        <!-- 密码-->
        <el-form-item prop='password'>
          <el-input v-model='loginForm.password' prefix-icon='iconfont icon-3702mima' type='password'></el-input>
        </el-form-item>
        <!-- 按钮区-->
        <el-form-item class='btns'>
          <el-button type='primary' @click='login'>登录</el-button>
          <el-button type='info' @click='resetLoginForm'>重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      // 登录表单的数据绑定对象
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      // 表单的验证规则
      loginFormRules: {
        // 验证用户名是否合法
        username: [
          {required: true, message: '请输入登录名称', trigger: 'blur'},
          {min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur'}
        ],
        // 验证密码是否合法
        password: [
          {required: true, message: '请输入登录密码', trigger: 'blur'},
          {min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    // 点击重置按钮，重置登录表单
    resetLoginForm() {
      // TODO 这里的 this 指向的是当前组件的 this
      console.log(this);
      this.$refs.loginFormRef.resetFields()
    },
    async login() {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
        // TODO 如果某个方法返回的是 Promise，那么前面添加一个 await
        // const result = await this.$http.post('login', this.loginForm)
        // data: res 这里的 res 是重命名
        const {data: res} = await this.$http.post('login/auth', this.loginForm)
        console.log(res)
        if (res.code !== 200) return this.$message.error('登录失败')
        this.$message.success('登录成功')
        // 1. 将登录成功之后的 token , 保存到客户端的 sessionStorage 中
        // 1.1 项目中除了登录之外的其他API接口，必须再登录之后才能访问
        // 1.2 token 只应该再当前网站打开期间生效，所以将 token 保存再 sessionStorage 中
        // 保存 token 到 sessionStorage
        // window.sessionStorage.setItem('token', res.data.token)
        // 2. 通过编程式导航跳转到后台主页，路由地址式 /home
        this.$router.push('/home')
      })
    }
  }
}
</script>

<!--scoped 只在当前组件中生效，去掉在全局生效。一个组件中的样式不应该影响其他地方-->
<style lang='less' scoped>
.login_container {
  background-color: #2b4b6b;
  height: 100%;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute; // 绝对定位，这样就可以位移了
  left: 50%;
  top: 50%;
  transform: translate(-50%);

  .avatar_box {
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
}
</style>
